<template>
  <div class="add">
    <el-page-header @back="goBack" content="Add Users"></el-page-header>
    <h2 style="text-indent:25px">Add users</h2>
    <div>
      <div style="margin: 20px;"></div>
      <el-form :label-position="labelPosition" label-width="80px" :model="user">
        <el-form-item label="Name">
          <el-input v-model="user.name"></el-input>
        </el-form-item>
        <el-form-item label="Phone">
          <el-input v-model="user.phone"></el-input>
        </el-form-item>
        <el-form-item label="email">
          <el-input v-model="user.email"></el-input>
        </el-form-item>
        <el-form-item label="education">
          <el-input v-model="user.education"></el-input>
        </el-form-item>
        <el-form-item label="profile">
          <el-input v-model="user.profile"></el-input>
        </el-form-item>
        <el-button type="primary" v-on:click="addUser ">ADD</el-button>
      </el-form>
    </div>
  </div>
</template>





<script>
export default {
  name: "Add",
  data() {
    return {
      //   user: {},
      labelPosition: "right",
      user: {
        name: "",
        phone: "",
        email: "",
        education: "",
        profile: ""
      }
    };
  },
  methods: {
    addUser(e) {
      if (!this.user.name || !this.user.phone) {
        this.$message.error("Error");
      } else {
        let newUser = {
          name: this.user.name,
          phone: this.user.phone,
          email: this.user.email,
          education: this.user.education,
          profile: this.user.profile
        };
        this.$http
          .post("http://localhost:3000/users", newUser)
          .then(response => {
            // console.log(response);
            this.$router.push({
              path: "/"
            });
            this.$message({
              message: "success",
              type: "success"
            });
          });
      }
    },
    goBack() {
      this.$router.replace("/");
    }
  }
};
</script>

<style scoped>
.el-button {
  float: right;
}
</style>





