<template>
  <div class="Edit">
    <el-page-header @back="goBack" content="Edit User"></el-page-header>
    <h2 style="text-indent:25px">Edit users</h2>
    <div>
      <div style="margin: 20px;"></div>
      <el-form :label-position="labelPosition" label-width="80px" :model="user">
        <el-form-item label="Name">
          <el-input v-model="user.name"></el-input>
        </el-form-item>
        <el-form-item label="Phone">
          <el-input v-model="user.phone"></el-input>
        </el-form-item>
        <el-form-item label="email">
          <el-input v-model="user.email"></el-input>
        </el-form-item>
        <el-form-item label="education">
          <el-input v-model="user.education"></el-input>
        </el-form-item>
        <el-form-item label="profile">
          <el-input v-model="user.profile"></el-input>
        </el-form-item>
        <el-button type="primary" v-on:click="updateUser">Update</el-button>
      </el-form>
    </div>
  </div>
</template>





<script>
export default {
  name: "Edit",
  data() {
    return {
      user: {
        name: "",
        phone: "",
        email: "",
        education: "",
        profile: ""
      },
      labelPosition: "right"
    };
  },
  methods: {
    fetchUser(id) {
      this.$http.get("http://localhost:3000/users/" + id).then(response => {
        // console.log(response);
        this.user = response.data;
        
      });
    },

    updateUser(e) {
      let updateUser = {
        name: this.user.name,
        phone: this.user.phone,
        email: this.user.email,
        education: this.user.education,
        profile: this.user.profile
      };
      this.$http
        .put("http://localhost:3000/users/" + this.$route.params.id, updateUser)
        .then(response => {
          // console.log(user.name);
          this.$router.push({
            path: "/"
          });
        });
    },

    goBack() {
      this.$router.replace("/");
    }
  },
  created(){
    this.fetchUser(this.$route.params.id);
  }
  

}
</script>

<style scoped>
.el-button {
  float: right;
}
</style>





