<template>
  <div class="users">
    <div>
      <!-- <h2 style="text-indent:25px">Management System</h2> -->
      <div class="search">
        <el-input v-model="input" placeholder="Keywords" style="width:200px" clearable></el-input>
        <!-- <input type="text" class="form-control" placeholder="search" v-model="filterInput"> -->
        <el-button type="primary" icon="el-icon-search" @click="searchUsers()">search</el-button>
      </div>

      <div class="btn">
        <el-button type="primary" v-on:click="addUser">新增</el-button>
        <!-- <el-button type="danger" v-on:click="deleteUser">删除</el-button> -->
      </div>
    </div>
    <el-col class="btn"></el-col>
    <div class="table">
      <el-table :data="users" style="width: 100%">
        <el-table-column fixed prop="id" label="id" width="80"></el-table-column>
        <el-table-column fixed prop="name" label="name" width="150"></el-table-column>
        <el-table-column prop="phone" label="phone" width="150"></el-table-column>
        <el-table-column prop="email" label="email" width="150"></el-table-column>
        <el-table-column prop="education" label="education" width="150"></el-table-column>
        <el-table-column prop="profile" label="profile" width="200"></el-table-column>
        <el-table-column fixed="right" label="操作" width="140">
          <template slot-scope="scope">
            <el-button @click="showUser(scope.row)" type="text" size="small">查看</el-button>
            <el-button @click="deleteUser(scope.row)" type="text" size="small">移除</el-button>
            <el-button @click="editUser(scope.row)" type="text" size="small">編輯</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "Users",
  data() {
    return {
      users: [],
      input: ""
    }
  },
  methods: {
    showUser(row) {
      // console.log(row.id);
      this.$router.replace("/details/" + row.id);
    },
    fetchUsers() {
      this.$http.get("http://localhost:3000/users").then(response => {
        // console.log(response);
        this.users = response.data;
      });
    },
    addUser() {
      this.$router.replace("/add");
    },
    deleteUser(row) {
      this.$http
        .delete("http://localhost:3000/users/" + row.id)
        .then(response => {
          this.$router.replace("/");
          this.fetchUsers();
          this.$message({
              message: "success",
              type: "success"
            });
        });
    },
    editUser(row) {
      this.$router.replace("/edit/" + row.id);
    },
    searchUsers() {
      // console.log(this.input);
      this.$http
        .get("http://localhost:3000/users?name=" + this.input)
        .then(response => {
          console.log(response);
          this.users = response.data;
        });
    }
  },
  created() {
    this.fetchUsers();
  }
};
</script>

<style scoped>
.search {
  float: left;
}
.btn {
  float: right;
  margin-right: 20px;
}

</style>





