<template>
  <div class="details">
    <el-page-header @back="goBack" content="Details"></el-page-header>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>Details</span>
      </div>
      <ul>
        <li>id: {{user.id}}</li>
        <li>name: {{user.name}}</li>
        <li>phone: {{user.phone}}</li>
        <li>email: {{user.email}}</li>
        <li>education: {{user.education}}</li>
        <li>profile: {{user.profile}}</li>
      </ul>
    </el-card>
  </div>
</template>




<script>
export default {
  name: "Details",
  data() {
    return {
      user: ""
    };
  },
  methods: {
    fetchCustomers(id) {
      this.$http.get("http://localhost:3000/users/" + id).then(response => {
        // console.log(response);
        this.user = response.data;
      });
    },
    goBack() {
      this.$router.replace("/");
    }
  },
  created() {
    this.fetchCustomers(this.$route.params.id);
  }
};
</script>
<style scoped>

.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.box-card {
  width: 480px;
  margin: 0 auto;
  /* margin-top: 25px; */
}
</style>



